.settings {
    display: flex;
    flex-direction: column;
    width: 474px;

    @media screen and (max-width: $screen-s) {
        width: 100%;
    }

    &__title {
        margin-bottom: var(--space-xs);
    }

    &__description {
        margin-bottom: var(--space-3xl);
    }
}