.icon {
    &::after {
        content: "";
        display: block;
        background-size: 100%;
    }

    
    &_settings::after {
        width: 12px;
        height: 12px;
        background-image: url("../img/settings.svg");
    }
    
    &_run-build::after {
        width: 9px;
        height: 10px;
        background-image: url("../img/run.svg");
    }
    
    &_rebuild::after {
        width: 12px;
        height: 12px;
        background-image: url("../img/rebuild.svg");
    }

    &_close::after {
        width: 16px;
        height: 16px;
        background-image: url("../img/close.svg");
    }

    &_branch::after {
        width: 16px;
        height: 8px;
        background-image: url("../img/branch.svg");
    }

    &_author::after {
        width: 13px;
        height: 14px;
        background-image: url("../img/author.svg");
    }

    &_calendar::after {
        width: 14px;
        height: 16px;
        background-image: url("../img/calendar.svg");
    }

    &_time::after {
        width: 14px;
        height: 16px;
        background-image: url("../img/time.svg");
    }

    &_completed::after {
        width: 22px;
        height: 22px;
        background-image: url("../img/completed.svg");
    }

    &_fail::after {
        width: 22px;
        height: 22px;
        background-image: url("../img/fail.svg");
    }

    &_processing::after {
        width: 22px;
        height: 22px;
        background-image: url("../img/processing.svg");
    }
}